<template>
  <scroll
    :data="topList"
    class="listtop"
    :pullup="true"
    :beforeScroll="true"
    @scrollToEnd="serachMove"
    @beforeScroll="listScroll"
    ref="toplist"
  >
    <ul ref="rank">
      <li
        @click="selectItem(index, item)"
        class="item"
        v-for="(item, index) in topList"
        :key="index"
      >
        <div class="icon">
          <img v-lazy="item.coverImgUrl">
        </div>
        <ul class="songlist">
          <div class="discList-con">
            <h2>{{item.name}}</h2>
            <p>{{item.updateFrequency}}</p>
          </div>
        </ul>
      </li>
      <loading v-show="hasMove" title></loading>
    </ul>

    <transition name="fade">
      <router-view></router-view>
    </transition>
  </scroll>
</template>
<script>
import { mapMutations } from "vuex";
import Scroll from "base/scroll/scroll";
import Loading from "base/loading/loading";
import { getToplist } from "api/rank";
// import { ERR_OK } from "api/config";
import { playlistMixin } from "assets/js/mixin";
export default {
  name: "Rank",
  mixins: [playlistMixin],
  components: {
    Scroll,
    Loading
  },
  data() {
    return {
      toplistdata: "",
      topList: []
    };
  },
  methods: {
    _getToplist() {
      getToplist().then(res => {
        this.toplistdata = res;
        console.log(this.toplistdata);
        this.topList = res.list;
      });
    },
    selectItem(index, item) {
      this.$router.push({
        path: `/toplist/${index}`
      });
      this.setDisc(item);
    },
    ...mapMutations({
      setDisc: "SET_DISC"
    })
  },
  created() {
    this._getToplist();
  }
};
</script>
<style lang="scss" scoped>
@import "assets/css/mixin.scss";
// .rank {
//   position: fixed;
//   width: 100%;
//   top: rem(88);
//   bottom: 0;
// }
.listtop {
  // height: 100%;
  width: 100vw;
  height: 86vh;
  overflow: hidden;
}
.item {
  display: flex;
  // margin: 0 rem(20);
  margin: rem(10) 0;
  height: rem(100);
}
.item:last-child {
  padding-bottom: rem(20);
}
.icon {
  flex: 0 0 rem(100);
  // width: 90%;
  // padding: 0 rem(20);
  height: rem(100);
  margin: 0 rem(10);
}
.icon img {
  max-width: 100%;
  max-height: 100%;
  height: auto;
  border-radius: rem(10);
}
.songlist {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 rem(20);
  height: rem(100);
  overflow: hidden;
  background: #333;
  color: rgba(255, 255, 255, 0.486);
  font-size: rem(13);
}
.song {
  max-height: rem(26);
  line-height: rem(26);
  overflow: hidden;
}
.song span {
  width: 100%;
  overflow: hidden;
}
.discList-con {
  padding-left: rem(20);
  overflow: hidden;
}
.discList-con h2 {
  margin-bottom: rem(10);
  color: #fff;
  font-size: rem(15);
  // padding-top: rem(16);
  line-height: rem(25);
}
.discList-con p {
  color: hsla(0, 0%, 100%, 0.3);
  line-height: rem(28);
  font-size: rem(14);
}
.loading-container {
  position: fixed;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
}
.lodAuto {
  text-align: center;
  margin-top: rem(10);
  color: rgba(206, 209, 212, 0.849);
  border-top: 1px solid rgba(255, 255, 255, 0.082);
  line-height: rem(40);
  font-size: rem(20);
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s;
}
.fade-enter,
.fade-leave-to {
  transform: translate3d(100%, 0, 0);
}
</style>



